body {
  background-color: #fff;
}
.dark body {
  background-color: rgb(13 36 56);
}
/* Scroll to top style */
.scrollToTop {
  @apply bg-indigo-600;
  @apply text-white;
  position: fixed;
  width: 100%;
  align-items: center;
  height: 20px;
  justify-content: center;
  z-index: 999;
  cursor: pointer;
  animation: fadeIn 0.3s;
  transition: opacity 0.4s;
  opacity: 0.9;
}
.dark .scrollToTop {
  color: white;
}
.overflow-hidden {
  overflow-x: hidden;
}
.scrollable-div {
  overflow-x: hidden;
  overflow-y: auto;
}

.scrollToTop:hover {
  opacity: 1;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}
